<template>
  <section>
    <el-row class="row">
      <el-col :span="20" :offset="2">
        <el-carousel indicator-position="outside" :interval="4000" height="400px">
          <el-carousel-item>
            <img src="/static/1.png" style="width:100%;height:100%">
          </el-carousel-item>
          <el-carousel-item>
            <img src=" /static/2.png" style="width:100%;height:100%">
          </el-carousel-item>
          <el-carousel-item>
            <img src="/static/3.png " style="width:100%;height:100%">
          </el-carousel-item>
        </el-carousel>
      </el-col>
    </el-row>
    <el-row :gutter="20" type="flex" justify="center" class="row">
      <el-col
        :span="4"
        v-for="(item,o) in cardContent"
        :key="o"
        shadow="hover"
        @mouseenter.native="cardActive(o,true)"
        @mouseleave.native="cardActive(o,false)"
      >
        <el-card class="box-card">
          <div class="bigText">{{$t(item.name)}}</div>
          <div v-show="!getCardActive(o)" class="contentText">{{$t(item.content1)}}</div>
          <div align="center">
            <el-button
              class="cardBtn"
              type="success"
              plain
              v-show="getCardActive(o)"
            >{{$t(item.content2)}}</el-button>
          </div>
        </el-card>
      </el-col>
    </el-row>
    <hr
      style="filter: alpha(opacity=100,finishopacity=0,style=3);margin:20px auto;"
      width="80%"
      color="#eef1f6"
      size="3"
    >
    <el-row type="flex" justify="center" class="row">
      <el-col :span="5">
        <div class="bigText">
          <i class="el-icon-view"></i>
          {{$t("main.News")}}
        </div>
      </el-col>
      <el-col :span="5" v-for="(news,index) in newsList" :key="index">
        <a class="contentText2" href="#">{{$t(news)}}</a>
      </el-col>
    </el-row>
    <hr
      style="filter: alpha(opacity=100,finishopacity=0,style=3) margin:20px auto;"
      width="80%"
      color="#eef1f6"
      size="3"
    >
    <el-row type="flex" justify="center" class="row">
      <el-col :span="24">
        <div class="bigText2">{{$t("main.OTCBTC")}}</div>
        <div class="contentText">{{$t("main.Supp")}}</div>
        <div class="contentText">{{$t("main.Order")}}</div>
        <el-row type="flex" justify="center" class="row">
          <el-col :span="5">
            <el-button type="success" class="cardBtn">{{$t("main.BuyBTC")}}</el-button>
          </el-col>
          <el-col :span="5" plain>
            <el-button type="success" plain class="cardBtn">{{$t("main.SellBTC")}}</el-button>
          </el-col>
        </el-row>
      </el-col>
    </el-row>
    <el-row type="flex" justify="center" class="row" :gutter="20">
      <el-col :span="6" v-for="o in 3" :key="o">
        <!-- {{tableData.length/3*(o-1)}}
        {{tableData.slice(tableData.length/3*(o-1),tableData.length/3*(o))}}-->
        <el-table
          :data="tableData.slice(tableData.length/3*(o-1),tableData.length/3*(o))"
          stripe
          :show-header="false"
        >
          <el-table-column prop="content" label="姓名" width="180"></el-table-column>
          <el-table-column prop="ctime" label="地址" align="right">
            <template slot-scope="scope">
              <el-button type="info" size="small">{{scope.row.ctime|getDateDetail}}</el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>

    <el-row type="flex" justify="center" class="row" :gutter="20">
      <el-col :span="6">
        <el-card>
          <div slot="header" class="clearfix">
            <span class="bigText3">{{$t("main.card1")}}</span>
          </div>
          <ul>
            <li class="textItem">{{$t("main.card1-1")}}</li>
            <li class="textItem">{{$t("main.card1-2")}}</li>
            <li class="textItem">{{$t("main.card1-3")}}</li>
            <li class="textItem">{{$t("main.card1-4")}}</li>
          </ul>
        </el-card>
      </el-col>
      <el-col :span="6">
        <el-card>
          <div slot="header" class="clearfix">
            <span class="bigText3">{{$t("main.card2")}}</span>
          </div>
          <ul>
            <li class="textItem">{{$t("main.card2-1")}}</li>
            <li class="textItem">{{$t("main.card2-2")}}</li>
            <li class="textItem">{{$t("main.card2-3")}}</li>
            <li class="textItem">{{$t("main.card2-4")}}</li>
          </ul>
        </el-card>
      </el-col>
      <el-col :span="6">
        <el-card>
          <div slot="header" class="clearfix">
            <span class="bigText3">{{$t("main.card3")}}</span>
          </div>
          <ul>
            <li class="textItem">{{$t("main.card3-1")}}</li>
            <li class="textItem">{{$t("main.card3-2")}}</li>
            <li class="textItem">{{$t("main.card3-3")}}</li>
            <li class="textItem">{{$t("main.card3-4")}}</li>
          </ul>
        </el-card>
      </el-col>
    </el-row>
  </section>
</template>
<script>
var self = this;
export default {
  data() {
    return {
      tableData: [
        {
          content: "CNY => 0.322 OBC",
          ctime: new Date("Tue, 13 Jan 2019 21:48:50 GMT").toUTCString()
        },
        {
          content: "CNY <= 18.571 EOS",
          ctime: new Date("Tue, 14 Jan 2019 21:48:50 GMT").toUTCString()
        },
        {
          content: "CNY => 6.198 ETH",
          ctime: new Date("Tue, 12 Jan 2019 21:48:50 GMT").toUTCString()
        },
        {
          content: "USD <= 0.322 ETH",
          ctime: new Date("Tue, 10 Jan 2019 21:48:50 GMT").toUTCString()
        },
        {
          content: "USD => 18.571 EOS",
          ctime: new Date("Tue, 12 Jan 2019 21:48:50 GMT").toUTCString()
        },
        {
          content: "CNY <= 6.198 OBC",
          ctime: new Date("Tue, 13 Jan 2019 21:48:50 GMT").toUTCString()
        },
        {
          content: "CNY => 0.322 OBC",
          ctime: new Date("Tue, 14 Jan 2019 21:48:50 GMT").toUTCString()
        },
        {
          content: "USD => 18.571 EOS",
          ctime: new Date("Tue, 15 Jan 2019 21:48:50 GMT").toUTCString()
        },
        {
          content: "CNY <= 6.198 OBC",
          ctime: new Date("Tue, 13 Jan 2019 21:48:50 GMT").toUTCString()
        }
      ],
      cardActiveNum: -1,
      newsList: ["main.news1", "main.news2", "main.news3"],
      cardContent: [
        {
          name: "main.OTC",
          content1: "main.Buy",
          content2: "main.BuyNow"
        },
        {
          name: "main.Exchange",
          content1: "main.Trade",
          content2: "main.TradeNow"
        },
        {
          name: "main.ExchangeAPI",
          content1: "main.Easy",
          content2: "main.ReadMe"
        }
      ]
    };
  },
  filters: {
    getDateDetail(date) {
      // console.log(date);
      var str1 = self.$t("main.ss");
      var str2 = self.$t("main.mm");
      var str3 = self.$t("main.hh");
      var str4 = self.$t("main.dd");
      // console.log(str1);
      var d = new Date(date);
      var year = d.getFullYear();
      var month = d.getMonth() + 1;
      var day = d.getDate();
      var hour = d.getHours();
      var minute = d.getMinutes();
      var sec = d.getSeconds();

      var dNow = new Date();
      var yearNow = dNow.getFullYear();
      var monthNow = dNow.getMonth() + 1;
      var dayNow = dNow.getDate();
      var hourNow = dNow.getHours();
      var minuteNow = dNow.getMinutes();
      var secNow = dNow.getSeconds();
      if (year == yearNow) {
        if (month == monthNow) {
          if (day == dayNow) {
            if (hour == hourNow) {
              if (minute == minuteNow) {
                return secNow - sec + str1;
              } else {
                return minuteNow - minute + str2;
              }
            } else {
              return hourNow - hour + str3;
            }
          } else {
            if (dayNow - day <= 10) {
              return dayNow - day + str4;
            }
          }
        }
      }
      return year + "-" + month + "-" + day;
    }
  },
  methods: {
    cardActive(o, flag) {
      if (flag) {
        this.cardActiveNum = o;
      } else {
        this.cardActiveNum = -1;
      }
    },
    getCardActive(o) {
      if (o == this.cardActiveNum) {
        return true;
      } else {
        return false;
      }
    }
  },
  mounted() {
    var user = sessionStorage.getItem("user");
  },
  created() {
    self = this;
  }
};
</script>
<style scoped>
.row {
  margin: 20px auto;
}
.el-carousel__item h3 {
  color: #475669;
  font-size: 18px;
  opacity: 0.75;
  line-height: 300px;
  margin: 0;
}

.el-carousel__item:nth-child(2n) {
  background-color: #99a9bf;
}

.el-carousel__item:nth-child(2n + 1) {
  background-color: #d3dce6;
}
.bigText {
  font-size: 18px;
  font-weight: 700;
  text-align: center;
  margin: 10px;
}
.bigText2 {
  font-size: 28px;
  font-weight: 700;
  text-align: center;
  margin: 10px;
}
.bigText3 {
  font-size: 18px;
  font-weight: 700;
  text-align: left;
  /* margin: 10px; */
}
.textItem {
  font-size: 14px;
  color: rgb(155, 155, 155);
  text-align: left;
  margin: 0 0 10px 0px;
}
.contentText {
  font-size: 16px;
  color: rgb(155, 155, 155);
  text-align: center;
  margin: 30px 0 10px 0;
}
.contentText2 {
  font-size: 16px;
  color: rgb(0, 172, 14);
  text-align: left;
  display: block;
  margin: 15px 0 10px 0;
}
.cardBtn {
  text-align: center;
  margin: 10px 0 10px 0;
  width: 80%;
}
.box-card {
  height: 150px;
}
</style>


