<template>
  <el-row class="container">
    <el-row :span="24" class="header">
      <el-col :span="5" class="logo" :class="'logo-width'">{{"CoinCreator"}}</el-col>
      <el-col :span="15">
        <el-menu
          :default-active="$route.path"
          class="el-menu-demo"
          @open="handleopen"
          @close="handleclose"
          @select="handleselect"
          mode="horizontal"
          unique-opened
          router
          v-show="!collapsed"
          background-color="#545c64"
          text-color="#fff"
          active-text-color="#ffd04b"
          v-for="(item,index) in $router.options.routes"
          v-if="!item.hidden&&item.path=='/coincreator'"
          :key="index"
        >
          <el-menu-item
            v-for="child in item.children"
            :index="child.path"
            :key="child.path"
            v-if="!child.hidden"
          >{{$t(child.name)}}</el-menu-item>
        </el-menu>
      </el-col>

      <el-col :span="4">
        <el-button round class="translate" @click="langToggle">en/中</el-button>
        <el-button round class="translate" @click="langToggle">en/中</el-button>
        <el-button round class="translate" @click="langToggle">en/中</el-button>
      </el-col>
    </el-row>

    <section class="content-container">
      <div class="grid-content bg-purple-light">
        <el-col :span="24" class="content-wrapper">
          <transition name="fade" mode="out-in">
            <router-view></router-view>
          </transition>
        </el-col>
      </div>
    </section>
  </el-row>
</template>


<script>
export default {
  data() {
    return {
      // sysName: "EXCHANGE",
      collapsed: false,
      sysUserName: "",
      sysUserAvatar: "",
      form: {
        name: "",
        region: "",
        date1: "",
        date2: "",
        delivery: false,
        type: [],
        resource: "",
        desc: ""
      }
    };
  },
  methods: {
    langToggle() {
      // console.log(this.$i18n.locale);
      if (this.$i18n.locale == "zh_CN") {
        this.$i18n.locale = "en_US";
      } else {
        this.$i18n.locale = "zh_CN";
      }
    },
    onSubmit() {
      console.log("submit!");
    },
    handleopen() {
      //console.log('handleopen');
    },
    handleclose() {
      //console.log('handleclose');
    },
    handleselect: function(a, b) {},
    //退出登录
    logout: function() {
      var _this = this;
      this.$confirm("确认退出吗?", "提示", {
        //type: 'warning'
      })
        .then(() => {
          sessionStorage.removeItem("user");
          _this.$router.push("/login");
        })
        .catch(() => {});
    },
    //折叠导航栏
    collapse: function() {
      this.collapsed = !this.collapsed;
    },
    showMenu(i, status) {
      this.$refs.menuCollapsed.getElementsByClassName(
        "submenu-hook-" + i
      )[0].style.display = status ? "block" : "none";
    }
  },
  mounted() {
    var user = sessionStorage.getItem("user");
    if (user) {
      user = JSON.parse(user);
      this.sysUserName = user.name || "";
      this.sysUserAvatar = user.avatar || "";
    }
  }
};
</script>

<style scoped lang="scss">
@import "../../styles/vars.scss";

.translate {
  margin-top: 10px;
  float: left;
}
.el-menu {
  width: 60%;
  background-color: #20a0ff;
  color: #fff;
  margin-left: 20%;
  .submenu {
    position: absolute;
    top: 0px;
    left: 60px;
    z-index: 99999;
    height: auto;
    display: none;
  }
  .el-menu--horizontal.el-menu--dark .el-submenu .el-menu-item.is-active,
  .el-menu-item.is-active {
    color: #000;
    border-bottom: 5px solid transparent;
  }
}
.container {
  position: absolute;
  top: 0px;
  bottom: 0px;
  width: 100%;
  .header {
    height: 60px;
    line-height: 60px;
    background: $color-primary;
    color: #fff;
    .userinfo {
      .el-button {
        width: 80px;
        height: 40px;
      }
      float: right;
    }

    .logo {
      //width:230px;
      height: 60px;
      font-size: 22px;
      text-align: center;
      padding-left: 20px;
      padding-right: 20px;
      // border-color: rgba(238, 241, 146, 0.3);
      // border-right-width: 1px;
      // border-right-style: solid;
      img {
        width: 40px;
        float: left;
        margin: 10px 10px 10px 18px;
      }
      .txt {
        color: #fff;
      }
    }
    .logo-width {
      width: 230px;
    }
    .logo-collapse-width {
      width: 60px;
    }
  }
  .main {
    display: flex;
    // background: #324057;
    position: absolute;
    top: 60px;
    bottom: 0px;
    overflow: hidden;

    .menu-expanded {
      flex: 0 0 230px;
      width: 230px;
      .el-menu {
        width: 100% !important;
      }
      .el-submenu .el-menu-item {
        min-width: 0px !important;
      }
    }
    .content-container {
      background: #f1f2f7;
      flex: 1;
      // position: absolute;
      // right: 0px;
      // top: 0px;
      // bottom: 0px;
      // left: 230px;

      overflow: scroll;
      padding: 20px;
      .content-wrapper {
        margin-top: 5%;
        background-color: #fff;
        box-sizing: border-box;
      }
    }
  }
}
</style>


