            <el-table-column :label="$t('BuyBTCDetail.Price')" prop="price"></el-table-column>
            <el-table-column :label="$t('BuyBTCDetail.TradingLimit')" prop="transLimit"></el-table-column>
            <el-table-column :label="$t('BuyBTCDetail.PaymentMethods')" prop="transMean"></el-table-column>
            <el-table-column :label="$t('BuyBTCDetail.PaymentWindow')" prop="expiration"></el-table-column>
            <el-table-column :label="$t('BuyBTCDetail.Status')" prop="stats"></el-table-column>
          </el-table>
          <hr
            style="filter: alpha(opacity=100,finishopacity=0,style=3);margin:10px auto;"
