                  style="width:20px;height:20px;margin:10px 10px 0 10px;"
                >
                <span>{{BTC+" BTC"}}</span>
              </div>
              <div class="rightDiv">
                <el-checkbox v-model="checked">{{$t('BuyBTCDetail.Ihave')}}</el-checkbox>
