              <el-card :body-style="{padding: '10px 20px'}" style="background-color:#bce8f1">

                <div class="normalText">{{'* '+$t('BuyBTCDetail.PS11')}}</div>
                <div class="normalText">{{'* '+$t('BuyBTCDetail.PS22')}}</div>
                <div class="normalText">{{'* '+$t('BuyBTCDetail.PS33')}}</div>
                <div class="normalText">{{'* '+$t('BuyBTCDetail.PS44')}}</div>
              </el-card>
