</template>
<script>
export default {
  data() {
    return {
      checked: false,
      CNY: "",
      BTC: "",
      rate: 100,
      userData1: [
        {
          username: "sdfsd",
          transNumber: "9146",
          transHistorical: "1140.43 BTC",
          favorComment: "99.94%",
          commentNumber: "+6662 / -4"
        }
      ],
      userData2: [
        {
          price: "24,490 CNY / BTC",
          transLimit: "24,490 CNY / BTC",
          transMean: "支付宝 银行卡",
          expiration: "15分钟",
          stats: "已上架"
        }
      ]
    };
  },
  computed: {},
  methods: {
    calBTC($event) {
      let CNY = parseFloat(this.CNY);
      let BTC = (CNY * 1.0) / this.rate;
      this.BTC = String(BTC);
    },
    calCNY($event) {
      let BTC = parseFloat(this.BTC);
      let CNY = BTC * this.rate;
      this.CNY = String(CNY);
    }
  }
};
</script>

<style scoped>
.cardBtn {
  float: right;
  font-size: 20px;
  text-align: center;
  margin: 10px 0 10px 0;
  width: 280%;
}
.rightDiv {
  float: right;
  margin: 1% auto;
  text-align: end;
}
.row {
  margin: 2% auto;
}
.bigText {
  font-size: 20px;
  font-weight: 700;
  text-align: left;
  margin: 20px 0;
}
.normalText {
  margin: 8px 0;
  font-size: 14px;
  line-height: 25px;
}
.normalText2 {
  margin: 5px 0;
  font-size: 14px;
  text-align: end;
}
.greenText {
  margin: 10px 0;
  font-size: 34px;
  text-align: end;
  color: green;
}
.greenText2 {
  margin: 10px 0;
  font-size: 14px;
  text-align: start;
  color: green;
}
.card {
  background-color: #f8f9fa;
}
</style>


