              </div>
            </div>
          </el-tab-pane>
          <el-tab-pane name="second">
            <span slot="label" class="spanCont">
              <i class="el-icon-date"></i>
              {{"ICO Contract"}}
            </span>
            <div class="tabCont">
              <div class="questionDiv">
                <span>{{"Coin or Token Name"}}</span>
                <el-input
                  placeholder="eg. ICO token"
                  v-model="input3"
                  class="input-with-select"
                  size="large"
                  style="font-size:25px"
                >
                  <template slot="prepend">
                    <el-tooltip
                      class="item"
                      effect="dark"
                      content="Right Center 提示文字"
                      placement="right"
                    >
                      <i class="el-icon-d-arrow-right"></i>
                    </el-tooltip>
                  </template>
                </el-input>
              </div>

              <div class="questionDiv">
                <span>{{"Coin or Token Name"}}</span>
                <el-input
                  placeholder="eg. ICO token"
                  v-model="input4"
                  class="input-with-select"
                  size="large"
                  style="font-size:25px;background-color: aliceblue !important;"
                >
                  <template slot="prepend">
                    <el-tooltip
                      class="item"
                      effect="dark"
                      content="Right Center 提示文字"
                      placement="right"
                    >
                      <i class="el-icon-d-arrow-right"></i>
                    </el-tooltip>
                  </template>
                </el-input>
              </div>
            </div>
          </el-tab-pane>
          <el-tab-pane name="third">
            <span slot="label" class="spanCont">
              <i class="el-icon-date"></i>
              {{"Bonuses"}}
            </span>
          </el-tab-pane>
          <el-tab-pane name="fourth">
            <span slot="label" class="spanCont">
              <i class="el-icon-date"></i>
              {{"Vesting"}}
            </span>
          </el-tab-pane>

          <el-tab-pane name="fifth">
            <span slot="label" class="spanCont">
              <i class="el-icon-date"></i>
              {{"Summary"}}
            </span>
            <el-col :span="24" class="tabName">{{'SETUP'}}</el-col>
            <el-col :span="12">
              <div class="questionDiv">
                <span>{{"Coin or Token Name"}}</span>
                <div class="chkInfo">{{input1||"空"}}</div>
              </div>
            </el-col>
            <el-col :span="12">
              <div class="questionDiv">
                <span>{{"Coin or Token Name"}}</span>
                <div class="chkInfo">{{input2||"空"}}</div>
              </div>
            </el-col>

            <el-col :span="24" class="tabName">{{'ICO CONTRACT'}}</el-col>
            <el-col :span="12">
              <div class="questionDiv">
                <span>{{"Coin or Token Name"}}</span>
                <div class="chkInfo">{{input3||"空"}}</div>
              </div>
            </el-col>
            <el-col :span="12">
              <div class="questionDiv">
                <span>{{"Coin or Token Name"}}</span>
                <div class="chkInfo">{{input4||"空"}}</div>
              </div>
            </el-col>
          </el-tab-pane>
        </el-tabs>
      </el-col>
      <el-col>
        <el-button type="primary">{{"Demonstrate"}}</el-button>
        <el-button type="primary">{{"Demonstrate"}}</el-button>
        <el-button type="primary">{{"Demonstrate"}}</el-button>
      </el-col>
    </el-col>

    <el-col :span="4">
      <el-card class="card">
        <img
          src="/static/user.png"
          align="center"
          style="display:block;width:30%;height:auto;margin:auto;"
        >
        <el-row type="flex" justify="center" class="btnRow">
          <el-button
            type="primary"
            class="cardBtn"
            @click="dialogFormVisible = true"
          >{{"Calculator"}}</el-button>
          <el-dialog title="收货地址" :visible.sync="dialogFormVisible" close="dialog">
            <span slot="title" class="spanCont">
              <i class="el-icon-date"></i>
              {{" Token Allocation Calculator"}}
            </span>
            <el-form :model="form">
              <el-row :gutter="20">
                <el-col :span="5">
                  <span class="spanCont">{{"Initial Supply"}}</span>
                  <el-input
                    placeholder="eg. ICO token"
                    v-model="form.name"
                    class="input-with-select"
                    style="font-size:14px"
                  ></el-input>
                </el-col>
                <el-col :span="5">
                  <span class="spanCont">{{"Tokens For Team"}}</span>
                  <el-input
                    placeholder="eg. ICO token"
                    v-model="form.name"
                    class="input-with-select"
                    style="font-size:14px"
                  ></el-input>
                </el-col>
                <el-col :span="6">
                  <span class="spanCont">{{"Token Supply (Hard Cap)"}}</span>
                  <div>
                    <span class="spanCont">6738</span>
                  </div>
                </el-col>
                <el-col :span="4">
                  <span class="spanCont">{{"Soft Cap "}}</span>
                  <div>
                    <span class="spanCont">6738</span>
                  </div>
                </el-col>
                <el-col :span="4">
                  <span class="spanCont">{{"Percent"}}</span>
                  <div>
                    <span class="spanCont">6738</span>
                  </div>
                </el-col>
              </el-row>
              <hr>
              <el-row>
                <el-table
                  :data="tableData"
                  stripe
                  style="width: 100%"
                  show-summary
                  :summary-method="sunmmaryTable"
                >
                  <el-table-column label="#"></el-table-column>
                  <el-table-column prop="tokenSold" label="TOKENS SOLD" width="180"></el-table-column>
                  <el-table-column prop="bouns" label="	BONUS (%)" width="180"></el-table-column>
                  <el-table-column prop="bounsToken" label="ONUS TOKENS"></el-table-column>
                  <el-table-column prop="total" label="TOTAL TOKENS OFFERED"></el-table-column>
                  <el-table-column prop="ofOffering" label="% OF OFFERING (HARD CAP)"></el-table-column>
                </el-table>
              </el-row>
              <hr>
              <el-alert type="warning" center :closable="false">
                <template slot="title">{{"Your token surplus/deficit is "}}</template>
              </el-alert>
              <!-- <el-form-item label="活动名称" :label-width="formLabelWidth">
                <el-input v-model="form.name" autocomplete="off"></el-input>
              </el-form-item>
              <el-form-item label="活动区域" :label-width="formLabelWidth">
                <el-select v-model="form.region" placeholder="请选择活动区域">
                  <el-option label="区域一" value="shanghai"></el-option>
                  <el-option label="区域二" value="beijing"></el-option>
                </el-select>
              </el-form-item>-->
            </el-form>
            <div slot="footer" class="dialog-footer">
              <el-button @click="dialogFormVisible = false">取 消</el-button>
              <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
            </div>
          </el-dialog>
        </el-row>

        <el-row type="flex" justify="center" class="btnRow">
          <el-button type="primary" class="cardBtn">{{"Demonstrate"}}</el-button>
        </el-row>
        <el-row type="flex" justify="center" class="btnRow">
          <el-button type="primary" class="cardBtn">{{"Demonstrate"}}</el-button>
        </el-row>
        <el-row type="flex" justify="center" class="btnRow">
          <el-button type="primary" class="cardBtn">{{"Demonstrate"}}</el-button>
        </el-row>
        <el-row type="flex" justify="center" class="btnRow">
          <el-button type="primary" class="cardBtn">{{"Demonstrate"}}</el-button>
        </el-row>
        <el-row type="flex" justify="center" class="btnRow">
          <el-button type="primary" class="cardBtn">{{"Demonstrate"}}</el-button>
        </el-row>
        <el-row>
          <div
            style="text-align:center;font-size:12px;margin-top:2%"
          >{{"Log in / Register to save your ICO Contracts"}}</div>
          <div
            style="text-align:center;margin-bottom:50%;font-size:18px;margin-top:5%"
          >{{" Hangzhou"}}</div>
        </el-row>
      </el-card>
    </el-col>
  </el-row>
</template>

<script>
export default {
  data() {
    return {
      a: 10,
      b: 12,
      tableData: [
        {
          tokenSold: "1",
          bouns: "3",
          bounsToken: "5",
          total: "6",
          ofOffering: "7"
        }
      ],
      dialogFormVisible: false,
      form: {
        name: "",
        region: "",
        date1: "",
        date2: "",
        delivery: false,
        type: [],
        resource: "",
        desc: ""
      },
      formLabelWidth: "120px",
      checked: false,
      input1: "",
      input2: "",
      input3: "",
      input4: "",
      input5: "",
      activeName2: "first",
      chkInfo:
        "The Token Only Option allows you to create just a token without a crowdsale. This is useful for issuing tokens directly for things such as event tickets, coupons, etc."
    };
  },
  methods: {
    sunmmaryTable(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        const values = data.map(item => Number(item[column.property]));
        if (!values.every(value => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          if (index === 0) {
            sums[index] = "TOTAL:";
          } else if (index === 5) {
            sums[index] += " %";
          } else if (index === 2 || index === 3) {
            sums[index] = "";
          }
        } else {
          sums[index] = "";
        }
      });
      sums[0] = "TOTAL: ";
      return sums;
    },
    handleClick(tab, event) {
      console.log(tab, event);
    }
  }
};
</script>
<style scoped>
.dialog span {
  margin: 5px 0 5px 0;
}
.tabName {
  color: deepskyblue;
  text-align: center;
  font-size: 40px;
}
.card {
  background-color: #92c2f1;
}
.el-checkbox {
  margin: 2%;
}
.btnRow {
  margin: 20px auto;
}
.row {
  margin: 40px auto;
}
.tabCont {
  padding: 1% 0 2% 0;
}
.chkCont {
  padding-left: 10px;
  font-size: 20px;
  margin-right: 5%;
}
.spanCont {
  font-size: 20px;
}
.bottomBtn {
  position: absolute;
  left: 0;
  bottom: 0;
}
.info {
  font-size: 20px;
  color: darkgrey;
}
.chkInfo {
  border: 1px solid #20a0ff;
  font-size: 20px;
  color: darkgrey;
  padding: 2%;
}
.questionDiv {
  margin: 40px auto;
}
.questionDiv div {
  margin: 10px auto;
}
i {
  margin: 0 3%;
}
.cardBtn {
  text-align: center;
  margin: 10px 0 0 0;
  width: 700%;
  font-size: 30px;
}
</style>



